.product-card {
	border-radius: 10px;
	overflow: hidden;
	position: relative;
	@media (min-width: 768px) {
	}
	.product-card__link {
		display: block;
	}
	.product-card__media {
		position: relative;
		.product-card__image-wrapper {
			width: 100%;
			@media (min-width: 768px) {
				height: 280px;
			}
			picture ,
			img {
				border-top-left-radius: 20px;
				border-top-right-radius: 20px;
				position: relative;
				display: inline-block;
				width: 100%;
				height: 100%;
				object-position: center;
				object-fit: cover;
			}
			picture + picture {
				display: none;
			}
		}
	}

	.product-card__body {
		background-color: #ffffff;
		border-bottom-left-radius: 20px;
		border-bottom-right-radius: 20px;
		padding: 16px 20px;
		display: flex;
		justify-content: space-between;
		gap: 8px;
		flex-direction: column;
		align-items: center;
		@media (min-width: 768px) {
			align-items: flex-start;
			flex-direction: row;
		}
		.product-card__title {
			font-family: "DM Sans";
			font-style: normal;
			font-weight: 600;
			font-size: 16px;
			line-height: 16px;
			color: #2a2a2a;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			text-align: center;
			@media (min-width: 768px) {
				text-align: left;
			}
		}
		.product-price {
			min-width: 82px;
			font-family: "DM Sans";
			font-style: normal;
			font-weight: 600;
			font-size: 16px;
			line-height: 16px;
			color: #2a2a2a;
			text-align: center;
			@media (min-width: 768px) {
				text-align: right;
			}
		}
	}

	.product-card__cta {
		display: none;
	}
}